import Vue from 'vue';
import VueRouter from "vue-router";
import LiveSample from "@/components/LiveSample";
import VodSample from "@/components/VodSample";

Vue.use(VueRouter);
export const router = new VueRouter({
    mode: 'history',
    routes:
        [
            {
                path: '/',
                component: LiveSample

            },
            {
                path: '/live',
                component: LiveSample

            },
            {
                path: '/vod',
                component: VodSample

            },


        ]

})
